.header {
  margin-top: .5rem;
  padding-left: 10px;
}

.header ul li {
  list-style: none;
  margin: 0;
  padding: .5rem 1rem;
  border-radius: 3px;
}

.header ul li:hover {
  background-color: #444;
}

.header a:hover {
  text-decoration: none;
}

.header h3 a {
  color: white;
}

.header h3 a:hover {
  color: #00bc8c;
}

@keyframes spinner {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-webkit-keyframes spinner {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes spinner_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}

@-webkit-keyframes spinner_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}

.spinner {
    position: relative;
    width:100%;
    height:100%;
    margin: auto;
}

.spinner div {
    position: absolute;
    width: 80px;
    height: 80px;
    top: 60px;
    left: 60px;
    border-radius: 50%;
    border: 4px solid #000;
    border-color: #fff transparent #fff transparent;
    -webkit-animation: spinner 1s linear infinite;
    animation: spinner 1s linear infinite;
}

.spinner div:nth-child(2) {
    width: 68px;
    height: 68px;
    top: 66px;
    left: 66px;
    border-color: transparent #00bc8c transparent #00bc8c;
    -webkit-animation: spinner_reverse 1s linear infinite;
    animation: spinner_reverse 1s linear infinite;
}

.spinner {
    width: 123px !important;
    height: 123px !important;
    -webkit-transform: translate(-61.5px, -61.5px) scale(0.615) translate(61.5px, 61.5px);
    transform: translate(-61.5px, -61.5px) scale(0.615) translate(61.5px, 61.5px);
}
.error-indicator {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #c78f22;
}

.error-indicator img {
    margin-bottom: 1rem;
}

.error-indicator .boom {
    font-size: 1.7rem;
}
.random-planet {
  display: flex;
  padding: 1rem;
  border: 1px solid #444;
}

.random-planet .planet-image {
  width: 150px;
  height: 150px;
  border-radius: 10px;
  margin-right: 1rem;

display: block;
position: relative;
text-align: center;
}

.random-planet .planet-image::after {
  content: "no image";

  font-size: 16px;
  color: rgb(100, 100, 100);

  display: block;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #303030;
}

.random-planet .list-group {
  margin: 1rem 0 0 1rem;
}

.random-planet .list-group-item {
  padding: .25rem;
}

.random-planet .list-group-item .term {
  margin-right: .5rem;
}
.toggle-planet {
  margin-bottom: 1rem;
}
.item-details {
  display: flex;
  flex-direction: row;
  padding: 1rem;
  border: 1px solid #444;
}

.item-details .item-image {
  width: 30%;
  height: 30%;
  border-radius: 10px;
  display: block;
  position: relative;
  text-align: center;
}

.item-details .item-image::after {
  content: "no image";

  font-size: 16px;
  color: rgb(100, 100, 100);

  display: block;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #303030;
}

.item-details .list-group-item {
  padding: .25rem;
}

.item-details .list-group-item .term{
  margin-right: .5rem;
}
.item-list {
  margin-bottom: 1rem;
}

.item-list .list-group-item {
  cursor: pointer;
}

.item-list .list-group-item:hover {
  background-color: #444;
}
